import React from "react";
import { ShopWindow } from "react-bootstrap-icons";
import { useNavigate } from "react-router-dom";
import { Dropdown } from "react-bootstrap";
const PharmacyDash = () => {
  const navigate = useNavigate();
  return (
    <>
      <div className="col-6 col-md-4 ">
        {" "}
        <div className="dashboard mb-2">
          <Dropdown style={{ width: "100%" }}>
            <Dropdown.Toggle
              style={{
                width: "100%",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
              variant="transparent"
            >
              <div>
                <div className="d-flex justify-content-center">
                  <ShopWindow className="dash-icon" />
                </div>
                <p className="text-center dash-text">Pharmacy</p>
              </div>
            </Dropdown.Toggle>
            <Dropdown.Menu>
              <Dropdown.Item>
                <p
                  className="btn-link"
                  onClick={() => {
                    navigate(`pharmacy-categories`);
                  }}
                >
                  Pharmacy Categories
                </p>
              </Dropdown.Item>

              <Dropdown.Item>
                <p
                  className="btn-link"
                  onClick={() => {
                    navigate(`pharmacy-products`);
                  }}
                >
                  Pharmacy Products
                </p>
              </Dropdown.Item>
              <Dropdown.Item>
                <p
                  className="btn-link"
                  onClick={() => {
                    navigate(`pharmacy-entries`);
                  }}
                >
                  Pharmacy Entries
                </p>
              </Dropdown.Item>

              <Dropdown.Item>
                <p
                  className="btn-link"
                  onClick={() => {
                    navigate(`pos`);
                  }}
                >
                  Pharmacy POS
                </p>
              </Dropdown.Item>
            </Dropdown.Menu>
          </Dropdown>
        </div>
      </div>
    </>
  );
};

export default PharmacyDash;
