import React from "react";
import { Book, CashCoin } from "react-bootstrap-icons";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
const ClientDash = () => {
  const Navigate = useNavigate();
  const ActiveCred = useSelector((state) => state.cred.activeCred);
  return (
    <>
      <div className="col-6 col-md-4 ">
        {" "}
        <div
          className="dashboard"
          onClick={() => {
            Navigate(`patients/patient/finance/${ActiveCred.linker}`);
          }}
        >
          <div className="d-flex justify-content-center">
            <CashCoin className="dash-icon" />
          </div>
          <p className="text-center dash-text">Finances</p>
        </div>
      </div>
      <div className="col-6 col-md-4 ">
        {" "}
        <div
          className="dashboard"
          onClick={() => {
            Navigate(`patients/patient/health/${ActiveCred.linker}`);
          }}
        >
          <div className="d-flex justify-content-center">
            <Book className="dash-icon" />
          </div>
          <p className="text-center dash-text">Health Records</p>
        </div>
      </div>
    </>
  );
};

export default ClientDash;
