import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import AdminDash from "../Admin/AdminDash";
import FinanceDash from "../Finance/FinanceDash";
import { useNavigate } from "react-router-dom";
import PharmacyDash from "../Pharmacy/PharmacyDash";
import StoreDash from "../Store/StoreDash";
import { EnvelopeOpenHeart, PersonHearts } from "react-bootstrap-icons";
import { FetchLiveSubs } from "../../Network/SubApi";
import ClientDash from "../Client/ClientDash";
import { VerifySub } from "../../Utils/VerifySub";

const HomeDash = () => {
  //redux dispatch
  const dispatch = useDispatch();
  const navigate = useNavigate();

  useEffect(() => {
    FetchLiveSubs(User.token, ActiveCred.instLinker, 0, dispatch);
  }, []);

  const User = useSelector((state) => state.auth.user);
  const ActiveCred = useSelector((state) => state.cred.activeCred);
  const subs = useSelector((state) => state.sub.subs);
  const Sub = VerifySub(subs, ActiveCred.instLinker);

  return Sub > 0 ? (
    <div className="row m-3 justify-content-center">
      {parseInt(ActiveCred.admin) === 1 ? <AdminDash></AdminDash> : null}
      {parseInt(ActiveCred.admin) === 1 ||
      parseInt(ActiveCred.finance) === 1 ? (
        <FinanceDash></FinanceDash>
      ) : null}
      {parseInt(ActiveCred.admin) === 1 ||
      parseInt(ActiveCred.finance) === 1 ||
      parseInt(ActiveCred.medical) === 1 ? (
        <>
          <PharmacyDash></PharmacyDash>
          <StoreDash></StoreDash>
          <div className="col-6 col-md-4 ">
            {" "}
            <div
              className="dashboard"
              onClick={() => {
                navigate(`patients`);
              }}
            >
              <div className="d-flex justify-content-center">
                <PersonHearts className="dash-icon" />
              </div>
              <p className="text-center dash-text">Patients</p>
            </div>
          </div>{" "}
          <div className="col-6 col-md-4 ">
            {" "}
            <div
              className="dashboard"
              onClick={() => {
                navigate(`appointments`);
              }}
            >
              <div className="d-flex justify-content-center">
                <EnvelopeOpenHeart className="dash-icon" />
              </div>
              <p className="text-center dash-text">Appointments</p>
            </div>
          </div>
        </>
      ) : null}
      {parseInt(ActiveCred.patient) === 1 ? <ClientDash></ClientDash> : null}
    </div>
  ) : (
    <></>
  );
};

export default HomeDash;
