import React from "react";
import {
  CashStack,
  Hdmi,
  HeartPulse,
  Hospital,
  PersonBadge,
  Robot,
} from "react-bootstrap-icons";
import { useNavigate } from "react-router-dom";
import { Dropdown } from "react-bootstrap";
const AdminDash = () => {
  const navigate = useNavigate();
  return (
    <>
      <div className="col-6 col-md-4 ">
        {" "}
        <div
          className="dashboard"
          onClick={() => {
            navigate(`staffs`);
          }}
        >
          <div className="d-flex justify-content-center">
            <PersonBadge className="dash-icon" />
          </div>
          <p className="text-center dash-text">Staffs</p>
        </div>
      </div>
      <div className="col-6 col-md-4 ">
        {" "}
        <div
          className="dashboard"
          onClick={() => {
            navigate(`modes`);
          }}
        >
          <div className="d-flex justify-content-center">
            <CashStack className="dash-icon" />
          </div>
          <p className="text-center dash-text">Payment Modes</p>
        </div>
      </div>
      <div className="col-6 col-md-4 ">
        {" "}
        <div className="dashboard mb-2">
          <Dropdown style={{ width: "100%" }}>
            <Dropdown.Toggle
              style={{
                width: "100%",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
              variant="transparent"
            >
              <div>
                <div className="d-flex justify-content-center">
                  <HeartPulse className="dash-icon" />
                </div>
                <p className="text-center dash-text">Services</p>
              </div>
            </Dropdown.Toggle>
            <Dropdown.Menu>
              <Dropdown.Item>
                <p
                  className="btn-link"
                  onClick={() => {
                    navigate(`service-categories`);
                  }}
                >
                  Service Categories
                </p>
              </Dropdown.Item>

              <Dropdown.Item>
                <p
                  className="btn-link"
                  onClick={() => {
                    navigate(`services`);
                  }}
                >
                  Services
                </p>
              </Dropdown.Item>
            </Dropdown.Menu>
          </Dropdown>
        </div>
      </div>
      <div className="col-6 col-md-4 ">
        {" "}
        <div
          className="dashboard"
          onClick={() => {
            navigate(`wards`);
          }}
        >
          <div className="d-flex justify-content-center">
            <Hospital className="dash-icon" />
          </div>
          <p className="text-center dash-text">Wards</p>
        </div>
      </div>
      <div className="col-6 col-md-4 ">
        {" "}
        <div
          className="dashboard"
          onClick={() => {
            navigate(`beds`);
          }}
        >
          <div className="d-flex justify-content-center">
            <Hdmi className="dash-icon" />
          </div>
          <p className="text-center dash-text">Ward Beds</p>
        </div>
      </div>
      <div className="col-6 col-md-4 ">
        {" "}
        <div
          className="dashboard"
          onClick={() => {
            navigate(`insurance-companies`);
          }}
        >
          <div className="d-flex justify-content-center">
            <Robot className="dash-icon" />
          </div>
          <p className="text-center dash-text">Accepted Insurance</p>
        </div>
      </div>
    </>
  );
};

export default AdminDash;
